@import url('https://fonts.googleapis.com/css2?family=Lora:wght@400;700&family=Roboto:wght@400;700&display=swap');


:root {
  --primary-color: #555;
  color: black;
  --card-bg: #f4f2ff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* Set Roboto as the default sans-serif font for body text */
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400; /* Regular weight for body */
  color: #40345c; /* Optional: adjust color for readability */
  line-height: 1.6; /* Optional: improve readability */
  background-color: #f4f2ff;
}

/* Set Lora as the serif font for titles and headings */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Lora', serif;
  font-weight: 700; /* Bold weight for headings */
  color: #222; /* Optional: make headings stand out */
}


.HomePage {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
}


/* Navbar Styling */
.navbar {
  display: flex;
  justify-content: space-between; /* Positions logo on the left, links on the right */
  align-items: center; /* Centers items vertically */
  position: sticky; /* Sticks to the top on scroll */
  top: 0; /* Ensures it stays at the top */
  z-index: 1000; /* Keeps it above other content */
}

/* Logo Styling */
.logo {
  font-size: 1.2em;
  font-weight: semibold;
  color: #555;
  letter-spacing: 0.75px;
}

/* Navigation Links Styling */
.nav-links {
  display: flex;
  gap: 50px; /* Space between links */
}

.nav-links a {
  color: #555; 
  text-decoration: none; 
  font-size: 1em;
  font-weight: 500;
  transition: color 0.3s ease; /* Smooth color transition */
}

.nav-links a:hover {
  transform: scale(1.11);
  font-weight: 600;
  color: #40345c; /* Changes color on hover for interactivity */
}




/* Hero Section (Picture + Text) Styling */
.hero {
    padding: 60px 20px;
    overflow: hidden;
  }
  
  .hero-image img {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
    float: left;
    margin: 0 20px 20px 0;
    shape-outside: circle(50%);
    clip-path: circle(50%);
    transition: transform 0.3s ease;
  }
  
  .hero-image img:hover {
    transform: scale(1.05);
  }
  
  .hero-text {
    max-width: 1000px;
  }
  
  .hero-text h1 {
    font-family: 'Source Serif 4', serif;
    font-size: 2.6em;
    font-weight: 600;
    color: #333;
    margin-bottom: 15px;
    line-height: 1.2;
  }
  
  .hero-text p {
    color: #555;
    line-height: 1.6;
    margin-top: 10px;
  }
  
  /* Responsive Adjustments for Smaller Screens */
  @media (max-width: 768px) {
    .hero {
      padding: 40px 10px;
    }
  
    .hero-image img {
      width: 180px;
      height: 180px;
      margin: 0 auto 20px;
      float: none;
      display: block;
      shape-outside: none;
      clip-path: circle(50%);
    }
  
    .hero-text h1 {
      font-size: 2em;
      text-align: center;
    }
  
    .hero-text p {
      font-size: 1em;
      text-align: center;
    }
  }
  
  @media (max-width: 480px) {
    .hero {
      padding: 20px 10px;
    }
  
    .hero-image img {
      width: 150px;
      height: 150px;
      margin: 0 auto 15px;
    }
  
    .hero-text h1 {
      font-size: 1.8em;
    }
  
    .hero-text p {
      font-size: 0.9em;
    }
  }
  


/* Section title */
.education h1 {
  font-size: 2em;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
}

/* Section Card */
.education-card {
  background-color: #40345c31;
  border-radius: 16px;
  padding: 30px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  width: 100%; /* Ensure container is full-width */
}

/* Degree title */
.education h2 {
  font-size: 1.6em;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
}

/* Specialization title */
.education-card h3 {
  font-size: 1.1em;
  font-weight: 600;
  color: #555;
  margin-top: 10px;
  margin-bottom: 15px;
}

/* Education description paragraph */
.education-card p {
  font-size: 1em;
  line-height: 1.6;
  color: #666;
  margin-top: 10px;
  margin-bottom: 15px;
}

.education-card a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.education-card a:hover {
  transform: scale(1.11);
  font-weight: 600;
  color: white;

}





/* Skills Section Styles */

/* Skills Title */
.skills h2 {
  font-size: 2em;
  margin-top: 80px;
  margin-bottom: 20px;
}

/* Pection Styles */
.projects h2 {
  font-size: 2em;
  margin-top: 60px;
}

/* Skills Card Space */
.cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

/* Skills Card Actual Card */
.skillscard {
  background-color: #40345c31;
  padding: 20px;
  border-radius: 12px;
  transition: transform 0.2s ease;
}

/* Skills Card Hover Animation */
.skillscard:hover {
  transform: scale(1.05);
}

/* Skills Card Title */
.card h3 {
  font-size: 1.3em;
  color: #333;
  margin-bottom: 10px;
}

/* Skills Card Description */
.card p {
  font-size: 1em;
  color: #666;
  margin-bottom: 15px;
}

/* Skills Card Footnote */
.tools {
  font-size: 0.9em;
  font-style: italic;
  font-weight: 600;
  color: black;
  margin-top: 10px;
}



