/* Windmate Card Styling */
.windmate_card {
  display: flex; /* Enables side-by-side layout for image and text */
  align-items: flex-start; /* Aligns image and text at the top */
  background-color: #40345c31;
  padding: 20px;
  border-radius: 12px;
  transition: transform 0.2s ease;
}

.windmate_card:hover {
  transform: scale(1.05);
}

/* Image Styling for Windmate */
.windmate img {
  width: 150px; /* Adjust size as needed */
  height: auto;
  border-radius: 8px; /* Optional rounded corners */
  margin-right: 20px; /* Space between image and text */
}

/* Text Content on the Right */
.windmate_card .text-content {
  display: flex;
  flex-direction: column; /* Stacks text vertically */
}

/* Title (h3) Styling */
.windmate_card h3 {
  font-size: 1.3em;
  color: #333;
  margin-top: 0;
  margin-bottom: 10px;
}

/* Description Text Styling */
.windmate_card p {
  font-size: 1em;
  color: #666;
  margin-bottom: 15px;
}





/* Responsive Adjustments */
@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    text-align: center;
  }

  .cards {
    grid-template-columns: 1fr;
  }
}

